@import url('https://fonts.googleapis.com/css?family=Cutive+Mono|Dosis&display=swap');

body {
  margin: 0;
  font-family: Cutive Mono, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


p {
  font-size: 1.2em;
}